<template>
    <div>
        Page B

                 <ul id="example-1">
                     <li v-for="item in forecasts">
                         {{ item.date }} - {{ item.summary }}
                     </li>
                 </ul>
                <button @click="refreshOne">Refresh 1</button>
    </div>
</template>
<script>
    import axios from "axios";

    export default {
        name: 'PageB',
        data: function() {
            return {
                forecasts: []
            };
        },
        mounted: function() {
            axios.get(window.appSettings.weatherApiUrl)
                .then(response => {
                        this.forecasts = response.data
                })
        },
        methods: {
            refreshOne: function () {
                axios.get(window.appSettings.weatherApiUrl)
                    .then(response => {
                        Object.assign(this.forecasts[1], response.data[1]);
                    })
            }
        }
    };
</script>